@import '~normalize.css';

* {
  box-sizing: inherit;
}
html {
  box-sizing: border-box;
}
body {
  font-family: "Nunito", "Arial", "Helvetica", sans-serif;
}

.container {
  max-width: 1024px;
  padding-left: 16px;
  padding-right: 16px;
  width: 95%;
  margin: 0 auto;
}

img {
  max-width: 100%;
}

.content {
  padding: 20px 0;
  flex-grow: 1;
  /* display: flex;
  justify-content: center;
  align-items: center; */
}

.content__sort {
  margin-bottom: 40px;
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* можно создать все переменные с цветами и далее их использовать в любом месте проекта */
:root {
  --text-main-color: #1A1A1A;

  --primary-main-color: #FFE44D;
  --primary-red-100: #F44336;
  --border-grey-10: #CFD8DC;
  --secondary-text: #7B8E98;
}
